.mainheading{
  display: flex;               
  justify-content: center;      
  height: 100vh;                
  text-align: center;          
  background-color: #f5f5f5; 
  
}
/* home css */
.hero-section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('../public/homepage.jpg');
  background-size: cover;
  background-position: center;
  text-align: center;
  color: white;
}

.hero-content {
  max-width: 600px;
}

.typing {
  font-weight: bold;
}

/* about css */
.about-section {
  padding: 40px 20px;
  background-color: #fff;
  text-align: center;
}

.about-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}
/* services */
.services-section {
  padding: 40px 20px;
  background-color: #eaeaea;
  text-align: center;
}

.services-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.service-item {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}
/* footer */
.footer {
  padding: 20px;
  background-color: #1a1a1a;
  color: white;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  nav ul {
    flex-direction: column;
    align-items: center;
  }

  .container {
    width: 90%;
  }
}

